import { useEffect, useState } from 'react';
import Alert from 'react-bootstrap/Alert';
import Button from 'react-bootstrap/Button';
import Card from 'react-bootstrap/Card';
import Col from 'react-bootstrap/Col';
import Form from 'react-bootstrap/Form';
import Row from 'react-bootstrap/Row';
import { useForm } from 'react-hook-form';
import { useNavigate } from 'react-router-dom';
import { toast } from "react-toastify";
import { useEditFormContext } from '../../../../context/EditFormContextProvider.js';
import { useTitle } from '../../../../helpers/custom-hooks.js';
import { processScaleOptions } from '../../../../helpers/form.helper.js';
import { getEditChildScales, updateChildScales } from '../../../../store/actions/form.action.js';
import { PageCard } from "../../../commonComponents/Cards/HelperCards.jsx";


const Child = () => {
	const navigate = useNavigate();
	const { getEditPatientId_c, getEditNavigateTo_c } = useEditFormContext();
	const patient_id = getEditPatientId_c();
	const EDIT_NAVIGATE_TO = getEditNavigateTo_c();
	const [isLoading, setIsLoading] = useState(false);
	const [alert, setAlert] = useState(true);
	const [sections, setSections] = useState([{ heading: '', description: '', options: [], section_questions: [] }]);
	const { register, handleSubmit, formState: { errors }, setValue } = useForm();



	useTitle('Edit Child Diagnostic Tools');
	useEffect(() => {
		window.scrollTo(0, 0);
		if (!patient_id) {
			navigate(EDIT_NAVIGATE_TO.home);
		} else {
			_getEditChildScales();
		}

	}, []);

	async function _getEditChildScales() {
		setIsLoading(true);
		const questions = await getEditChildScales({ id: patient_id });
		if (questions.status === 200) {
			// setting dafult values
			var regex = /^[\u0600-\u06FF]/;  // regix to check for the first urdu letter
			questions.data.section.forEach(section => {
				section.section_answers.forEach(answerObject => {
					setValue(`${patient_id}-${answerObject.section_id}-${answerObject.section_question_id}`, `${answerObject.section_option_id}-${parseInt(regex.test(answerObject.answer_text) ? 1 : 0)}-${answerObject.answer_text}`);
				});
			});
			setSections(questions.data.section);
			setIsLoading(false);
		} else if (questions.status >= 400) {
			toast.error(questions.message.message);
		}
	}

	async function handleData(data) {
		setIsLoading(true);
		const finalObject = processScaleOptions({ patient_id, data });
		const response = await updateChildScales({ patient_id, scales: finalObject.data });
		if (response.status === 200) {
			toast.success(response.message.message);
			navigate(EDIT_NAVIGATE_TO.home);
		} else if (response.status >= 400) {
			toast.error(response.message.message);
		}
		setIsLoading(false);
	}


	return (
		<PageCard title={`Edit Child Diagnostic Tools`} isLoading={isLoading} paddingSmall={true} >

			{alert ? <Alert variant="info" dismissible onClose={() => setAlert(false)} >
				<Alert.Heading>Child Diagnostic Tools</Alert.Heading>
				<hr />
				<p className="mb-0">
					This is Child's Diagnostic Tools data form. Please only fill Child's details here.
				</p>
			</Alert> : null}


			<div style={isLoading ? { pointerEvents: `none`, opacity: 0.5 } : {}}>
				<Form onSubmit={handleSubmit(handleData)} className='mt-10'>

					{sections.map((section, index) => (
						<>
							<Card className="mb-3 border border-3 p-3" key={index}>
								<div className='d-flex justify-content-center'>
									<Card.Title>{section.heading}</Card.Title>
								</div>
								{/* Section Discription */}
								<div className="mb-2 text-muted">
									{section.description?.split(',')?.map((questionDescription, index) => (
										<>
											<div className='px-auto' key={index}>
												{section.is_description_in_urdu ?
													<div className='d-flex justify-content-end'>
														<p key={index} className=''>{questionDescription}</p>
													</div>
													:
													<p key={index} className=''>{questionDescription}</p>
												}
											</div>
										</>
									))}
								</div>



								<Card.Body className='p-0'>

									{/* Section Options */}

									<Row className='text-bold'>
										<Col className='col-1'>
											No.
										</Col>
										<Col className='col-4'>
											Questions
										</Col>
										<Col className='m-0 p-0'>
											<Row className='m-0 p-0'>
												{section.options.map((option, i) => (
													<Col key={i} className='small p-0 m-0'>
														{option.option_text}
													</Col>
												))}
											</Row>
										</Col>
									</Row>


									{/* Section Questions */}
									{section.section_questions.map((question, index) => (
										<>
											<Row className='mt-5' key={index}>
												<Col className='col-1'>
													{index + 1}
												</Col>
												<Col className='col-4'>
													{question.is_question_in_urdu ? <div className='d-flex justify-content-end'>{question.question_text}</div> : question.question_text}
												</Col>
												<Col className='m-0 p-0'>
													<Row className='m-0 p-0'>
														{section.options.map((option, i) => (
															<Col className='small p-0 m-0' key={i}>
																<Form.Check type="radio" {...register(`${patient_id}-${section.id}-${question.id}`, { required: !question.is_nullable })} value={`${option.id}-${Number(option.is_option_in_urdu)}-${option.option_text}`} />
															</Col>
														))}
													</Row>
												</Col>
											</Row>
											{errors[`${patient_id}-${section.id}-${question.id}`] && <span className='text-danger ps-20'>This field is required</span>}
										</>
									))}
								</Card.Body>
							</Card>
						</>
					))}

					<Form.Group className="mb-3">
						<Button className="mb-3" variant="secondary" type="button" onClick={() => navigate(EDIT_NAVIGATE_TO.caregiverScale, { state: { id: patient_id } })}>
							Back
						</Button>
						<Button className="mb-3 float-end" variant="primary" type="submit">
							Submit
						</Button>
					</Form.Group>

				</Form>
			</div>


		</PageCard>
	);
};

export default Child;